/* src/components/Home.css */
.home-container {
    padding: 60px 20px 20px;
    background-color: #121212;
    color: #ffffff;
    text-align: center;
}

.hero-section {
    position: relative;
    text-align: center;
    color: white;
    margin-bottom: 40px;
    height: 70vh;
    overflow: hidden;
}

.hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.7;
    transition: transform 0.3s ease;
}

.hero-section:hover .hero-image {
    transform: scale(1.05);
}

.hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.7);
    padding: 30px;
    border-radius: 10px;
    width: 90%;
    max-width: 600px;
    box-sizing: border-box;
    z-index: 1;
    transition: background 0.3s ease;
}

.hero-text:hover {
    background: rgba(0, 0, 0, 0.8);
}

.hero-text h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.content-section {
    margin-top: 40px;
    padding: 30px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.content-section h2 {
    margin-bottom: 20px;
    font-size: 2rem;
    color: #ffd700;
}

.content-section p, .content-section ul {
    margin-bottom: 20px;
    font-size: 1.1rem;
    line-height: 1.6;
}

.content-section ul {
    list-style-type: none;
    padding: 0;
}

.content-section ul li {
    background: linear-gradient(to right, #333, #444);
    padding: 15px;
    margin: 10px 0;
    border-radius: 5px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.content-section ul li:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .hero-section {
        height: 50vh;
    }

    .hero-text {
        padding: 20px;
    }

    .hero-text h1 {
        font-size: 2rem;
    }

    .content-section h2 {
        font-size: 1.8rem;
    }

    .content-section p, .content-section ul {
        font-size: 1rem;
    }
}

@media (max-width: 480px) {
    .hero-section {
        height: 40vh;
    }

    .hero-text {
        width: 95%;
        padding: 15px;
    }

    .hero-text h1 {
        font-size: 1.8rem;
    }

    .content-section h2 {
        font-size: 1.6rem;
    }

    .content-section p, .content-section ul {
        font-size: 0.9rem;
    }
}
